import React from 'react';
import {useMetronome} from 'react-metronome-hook';
import { Button, Input} from "@vkontakte/vkui";

import Icon28Play from '@vkontakte/icons/dist/28/play';
import Icon28Pause from '@vkontakte/icons/dist/28/pause';

import click1 from './palmaF.mp3';
import click2 from './cajonS.mp3';

export default function MetronomeComponent(){

    const {
        startMetronome,
        isTicking,
        stopMetronome,
        bpm,
        beatsPerMeasure,
        setBpm,
        setBeatsPerMeasure,
        setSounds
    } = useMetronome(120, 4, [click1, click2]);

    return (
        <div className='m-4'>
            <Button
                before={isTicking ? <Icon28Pause/> : <Icon28Play/>}
                level='secondary' size='l'
                    className='mb-4'
                    onClick={isTicking ? stopMetronome : startMetronome}>
                    {isTicking ? "Stop" : "Start"}
            </Button>
            <div className='mb-4'>
                <p>BeatsPerMinuts: <span style={{color: 'tomato'}}>{bpm}</span></p>
                </div>
            <div className='mb-4'>
                <p>BeatsPerMeasure: <span style={{color: 'tomato'}}>{beatsPerMeasure}</span></p>
                </div>

            <Input className='mb-4' placeholder="Change BPM" onChange={e => setBpm(e.target.value)} />
            <Input className='mb-4' placeholder="Change beats per measure" onChange={e => setBeatsPerMeasure(e.target.value)} />
            <Button level='secondary' size='l'
                    onClick={() => setSounds([click1, click2])}>Изменить звук</Button>
        </div>
    );

}